import React, { Component } from 'react'
import _map from 'lodash/map'
import SocialLink from './SocialLink'
import socialPlugin from '../configs/socialPlugin.config'

export default class SocialList extends Component {
  render() {
    const { socialList } = this.props

    return (
      <section className="projects">
        {_map(socialList, project => {
          return (
            <div className="each" key={project.title}>
              <h2>
                <a
                  className="project-link"
                  href={project.source}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <div className="project-icon">
                    <SocialLink socialMedia={project.id} />
                  </div>
                  <div className="project-title">{project.title}</div>
                </a>
              </h2>
              <p>{project.description}</p>
              {/* <div className="buttons">
                {socialPlugin[project.id] && (
                  socialPlugin[project.id]
                )}
              </div> */}
            </div>
          )
        })}
      </section>
    )
  }
}
